<template>
  <section class="p-6 max-w-5xl mx-auto">
    <!-- ÏÉÅÎã® Ïï°ÏÖò ÎùºÏù∏ -->
    <div class="form-action">
      <MaterialButton color="dark" size="sm" variant="outlined" @click="goBack">
        ‚Üê Î™©Î°ùÏúºÎ°ú
      </MaterialButton>

      <!-- Ïò§Î•∏Ï™Ω: ÏÉÅÌÉúÏóê Îî∞Î•∏ Ïï°ÏÖò Î≤ÑÌäº Î¨∂Ïùå -->
      <div class="flex items-center gap-2">
        <!-- ÏûëÏÑ±ÌïòÍ∏∞ (Îã¥ÎãπÏûêÎßå / CB1, CB2) -->
        <MaterialButton
          v-if="role === 2 && (status === 'CB1' || status === 'CB2')"
          color="dark"
          size="sm"
          @click="goWrite"
        >
          ÏûëÏÑ±ÌïòÍ∏∞
        </MaterialButton>

        <!-- CB3 ‚Üí ÏàòÏ†ïÌïòÍ∏∞ (Îã¥ÎãπÏûêÎßå) -->
        <MaterialButton
          v-else-if="role === 2 && status === 'CB3'"
          color="dark"
          size="sm"
          @click="goEdit"
        >
          ÏàòÏ†ïÌïòÍ∏∞
        </MaterialButton>
      </div>
    </div>

    <!-- Î∞îÍπ• ÏÉÅÏÑ∏ Ïπ¥Îìú -->
    <div class="detail-card">
      <!-- Ìó§Îçî -->
      <header class="flex justify-between items-start detail-header">
        <div>
          <h2 class="text-2xl font-semibold mb-1">ÏÉÅÎã¥ ÏÉÅÏÑ∏</h2>
        </div>

        <!-- ÏÉÅÌÉú Î±ÉÏßÄ -->
        <span class="status-pill" :class="statusClass(status)">
          {{ statusLabel(status) }}
        </span>
      </header>

      <!-- Î°úÎî©/ÏóêÎü¨ -->
      <div v-if="loading" class="text-sm text-gray-500 py-6">
        Î∂àÎü¨Ïò§Îäî Ï§ë...
      </div>
      <div v-else-if="error" class="text-sm text-red-600 py-6">
        {{ error }}
      </div>

      <!-- Î≥∏Î¨∏ -->
      <template v-else>
        <!-- Í∏∞Î≥∏Ï†ïÎ≥¥ (ÏÉÅÌÉúÏôÄ Í¥ÄÍ≥ÑÏóÜÏù¥ Ìï≠ÏÉÅ ÌëúÏãú) -->
        <div class="meta-card">
          <div class="meta-grid">
            <!-- 1. ÏßÄÏõêÏûê -->
            <div class="meta-item">
              <span class="meta-label">ÏßÄÏõêÏûê</span>
              <span class="meta-value">
                {{ submitInfo.childName || "Î≥∏Ïù∏" }}
              </span>
            </div>

            <!-- 2. Î≥¥Ìò∏Ïûê -->
            <div class="meta-item">
              <span class="meta-label">Î≥¥Ìò∏Ïûê</span>
              <span class="meta-value">
                {{ submitInfo.guardianName || "-" }}
              </span>
            </div>

            <!-- 3. Îã¥ÎãπÏûê -->
            <div class="meta-item">
              <span class="meta-label">Îã¥ÎãπÏûê</span>
              <span class="meta-value">
                {{ submitInfo.assigneeName || "-" }}
              </span>
            </div>

            <!-- 4. Ïû•Ïï†Ïú†Ìòï -->
            <div class="meta-item">
              <span class="meta-label">Ïû•Ïï†Ïú†Ìòï</span>
              <span class="meta-value">
                {{ submitInfo.disabilityType || "-" }}
              </span>
            </div>

            <!-- 5. Ïö∞ÏÑ†ÏàúÏúÑ -->
            <div class="meta-item">
              <span class="meta-label">Ïö∞ÏÑ†ÏàúÏúÑ</span>
              <span class="meta-value">
                {{ priorityLabel }}
              </span>
            </div>

            <!-- 6. Ï°∞ÏÇ¨ÏßÄ Ï†úÏ∂úÏùº (Î≤ÑÌäº) -->
            <div class="meta-item">
              <span class="meta-label">Ï°∞ÏÇ¨ÏßÄ Ï†úÏ∂úÏùº</span>
              <span class="meta-value">
                <MaterialButton
                  color="dark"
                  size="sm"
                  @click="openSubmissionDetail"
                >
                  {{ formattedSubmitAt }}
                </MaterialButton>
              </span>
            </div>
          </div>
        </div>

        <!-- CB1 / CB2 Ïùº Îïå: ÎÇ¥Ïö© Ïà®Í∏∞Í≥† ÏïàÎÇ¥Îßå -->
        <div
          v-if="status === 'CB1' || status === 'CB2'"
          class="info-card muted-info"
        >
          <template v-if="status === 'CB1'">
            Ïù¥ ÏÉÅÎã¥ÏùÄ <strong>ÏûÑÏãú Ï†ÄÏû•</strong> ÏÉÅÌÉúÏûÖÎãàÎã§.<br />
          </template>

          <template v-else>
            Ïù¥ ÏÉÅÎã¥ÏùÄ ÏïÑÏßÅ <strong>ÏûëÏÑ± Ï†Ñ</strong> ÏÉÅÌÉúÏûÖÎãàÎã§.<br />
          </template>
        </div>

        <!-- CB1/CB2 Í∞Ä ÏïÑÎãê Îïå: Ïã§Ï†ú ÏÉÅÎã¥ ÎÇ¥Ïö© / Í∏∞Î°ù / ÏäπÏù∏/Î∞òÎ†§ ÎÖ∏Ï∂ú -->
        <template v-else>
          <!-- Î©îÏù∏ ÏÉÅÎã¥ -->
          <div class="block-card">
            <h3 class="block-title">Ï≤´ ÏÉÅÎã¥</h3>

            <div class="text-sm text-gray-600 mb-2">
              ÏÉÅÎã¥Ïùº:
              <span class="font-medium">{{ mainForm.counselDate || "-" }}</span>
            </div>

            <div class="field-block">
              <div class="field-label">ÏÉÅÎã¥ Ï†úÎ™©</div>
              <div class="field-value">
                {{ mainForm.title || "-" }}
              </div>
            </div>

            <div class="field-block">
              <div class="field-label">ÏÉÅÎã¥ ÎÇ¥Ïö©</div>
              <div class="field-value whitespace-pre-line">
                {{ mainForm.content || "-" }}
              </div>
            </div>

            <!-- Ï≤®Î∂Ä ÌååÏùº ÏòÅÏó≠ -->
            <div class="field-block mt-3">
              <div class="field-label">Ï≤®Î∂Ä ÌååÏùº</div>

              <div v-if="attachments.length">
                <ul class="list-disc pl-4 text-sm">
                  <li
                    v-for="file in attachments"
                    :key="file.attachCode"
                    class="text-blue-600"
                  >
                    <a
                      :href="file.url"
                      target="_blank"
                      class="hover:underline break-all"
                    >
                      {{ file.originalFilename }}
                    </a>
                  </li>
                </ul>
              </div>
              <div v-else class="text-xs text-gray-400">
                Ï≤®Î∂ÄÎêú ÌååÏùºÏù¥ ÏóÜÏäµÎãàÎã§.
              </div>
            </div>
          </div>

          <!-- Ï∂îÍ∞Ä ÏÉÅÎã¥ Í∏∞Î°ù -->
          <div class="space-y-4 mt-4">
            <h3 class="font-semibold text-lg">Ï∂îÍ∞Ä ÏÉÅÎã¥ Í∏∞Î°ù</h3>

            <template v-if="records.length">
              <div
                v-for="(record, idx) in records"
                :key="record.id || idx"
                class="block-card"
              >
                <div
                  class="flex justify-between items-center text-sm mb-2 border-b border-gray-200 pb-2"
                >
                  <div class="font-medium">Í∏∞Î°ù #{{ idx + 1 }}</div>
                  <div class="text-gray-500">
                    ÏÉÅÎã¥Ïùº:
                    <span class="font-medium">
                      {{ record.counselDate || "-" }}
                    </span>
                  </div>
                </div>

                <div class="field-block">
                  <div class="field-label">ÏÉÅÎã¥ Ï†úÎ™©</div>
                  <div class="field-value">
                    {{ record.title || "-" }}
                  </div>
                </div>

                <div class="field-block">
                  <div class="field-label">ÏÉÅÎã¥ ÎÇ¥Ïö©</div>
                  <div class="field-value whitespace-pre-line">
                    {{ record.content || "-" }}
                  </div>
                </div>
              </div>
            </template>
            <p v-else class="text-sm text-gray-500">
              Ï∂îÍ∞Ä ÏÉÅÎã¥ Í∏∞Î°ùÏù¥ ÏóÜÏäµÎãàÎã§.
            </p>
          </div>
        </template>
      </template>
    </div>

    <!-- ‚õî ÎßàÏßÄÎßâ Î∞òÎ†§ Ïù¥Î†• (ÏûàÏùÑ ÎïåÎßå ÎÖ∏Ï∂ú) -->
    <div
      v-if="rejectionInfo.reason && (status === 'CB4' || status === 'CB6')"
      class="rejection-card"
    >
      <div class="font-semibold mb-1 text-sm">Î∞òÎ†§ Ïù¥Î†•</div>

      <div class="mb-1">
        Î∞òÎ†§ÏùºÏûê:
        <span class="font-medium">
          {{ formattedRejectionDate }}
        </span>
      </div>

      <div>
        <div class="font-medium">ÏÇ¨Ïú†:</div>
        <p class="whitespace-pre-line mt-1">
          {{ rejectionInfo.reason }}
        </p>
      </div>
    </div>

    <!-- CB4 ‚Üí Ïû¨ÏàòÏ†ïÌïòÍ∏∞ (Î∞òÎ†§ Ïãú Îã¥ÎãπÏûê Ï†ÑÏö©) -->
    <div class="right-wrap mt-2">
      <MaterialButton
        v-if="role === 2 && status === 'CB4'"
        color="dark"
        size="sm"
        @click="goEdit"
      >
        Ïû¨ÏàòÏ†ïÌïòÍ∏∞
      </MaterialButton>
    </div>

    <!-- üî• Í¥ÄÎ¶¨Ïûê(3) Ï†ÑÏö© ÏòÅÏó≠: Î∞òÎ†§ Ïù¥Î†• + ÏäπÏù∏/Î∞òÎ†§ Î≤ÑÌäº -->
    <div
      v-if="role === 3 && (status === 'CB3' || status === 'CB6')"
      class="pt-4 border-t mt-2 space-y-3"
    >
      <!-- ÏäπÏù∏/Î∞òÎ†§ Î≤ÑÌäº -->
      <div class="approve-actions">
        <MaterialButton
          color="dark"
          size="sm"
          class="px-4"
          @click="handleApprove"
        >
          ÏäπÏù∏
        </MaterialButton>
        <MaterialButton
          color="dark"
          size="sm"
          class="px-4"
          @click="handleReject"
        >
          Î∞òÎ†§
        </MaterialButton>
      </div>
    </div>

    <!-- Î∞òÎ†§ ÏÇ¨Ïú† ÏûÖÎ†• Î™®Îã¨ -->
    <div v-if="rejectModalOpen" class="modal-overlay">
      <div class="modal-container">
        <h3 class="text-lg font-semibold mb-3">Î∞òÎ†§ ÏÇ¨Ïú† ÏûÖÎ†•</h3>

        <MaterialTextarea
          id="reject-reason"
          variant="outline"
          :rows="4"
          placeholder="Î∞òÎ†§ ÏÇ¨Ïú†Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî..."
          :value="rejectReason"
          @input="(e) => (rejectReason = e.target.value)"
        />

        <div class="modal-actions">
          <MaterialButton color="dark" size="sm" @click="closeRejectModal">
            Ï∑®ÏÜå
          </MaterialButton>
          <MaterialButton color="dark" size="sm" @click="confirmReject">
            Î∞òÎ†§
          </MaterialButton>
        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import { ref, computed } from "vue";
import { useRoute, useRouter } from "vue-router";
import axios from "axios";
import MaterialButton from "@/components/MaterialButton.vue";
import MaterialTextarea from "@/components/MaterialTextarea.vue";
import { useAuthStore } from "@/store/authLogin";

const route = useRoute();
const router = useRouter();

const auth = useAuthStore(); // üîπ Ï∂îÍ∞Ä
auth.reload();

const submitCode = computed(() =>
  Number(route.params.submitCode || route.query.submitCode || 0)
);

const loading = ref(false);
const error = ref("");
const attachments = ref([]); // Ï≤®Î∂ÄÌååÏùº Î™©Î°ù

// ÏøºÎ¶¨Î°ú ÎÑòÏñ¥Ïò® role (2: Îã¥ÎãπÏûê, 3: Í¥ÄÎ¶¨Ïûê, 4: ÏãúÏä§ÌÖú)
const role = computed(() => Number(route.query.role || 0));

const submitInfo = ref({
  name: "",
  ssnFront: "",
  submitAt: "",
});

const formattedSubmitAt = computed(() => {
  const v = submitInfo.value.submitAt;
  return v ? v.slice(0, 10) : "-";
});

const mainForm = ref({
  counselDate: "",
  title: "",
  content: "",
});

const records = ref([]);
const priority = ref("BB3");
const status = ref("");

// üîª ÎßàÏßÄÎßâ Î∞òÎ†§ Ïù¥Î†• (reason + date)
const rejectionInfo = ref({
  reason: "",
  date: "",
});

const formattedRejectionDate = computed(() => {
  const v = rejectionInfo.value.date;
  return v ? String(v).slice(0, 10) : "-";
});

const priorityLabel = computed(() => {
  const code = (priority.value || "").toString().toUpperCase();
  switch (code) {
    case "BB1":
      return "Í∏¥Í∏â";
    case "BB2":
      return "Ï§ëÏ†ê";
    case "BB3":
      return "Í≥ÑÌöç";
    default:
      return priority.value || "-";
  }
});

// Îç∞Ïù¥ÌÑ∞ Î°úÎî©
async function loadData() {
  loading.value = true;
  error.value = "";

  try {
    const code = submitCode.value;
    const { data } = await axios.get(`/api/counsel/${code}`);

    if (!data?.success || !data.result) {
      throw new Error(data?.message || "ÏÉÅÎã¥ Ï†ïÎ≥¥Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.");
    }

    const res = data.result;

    submitInfo.value = res.submit_info || submitInfo.value;

    mainForm.value = {
      counselDate: res.main?.counsel_date || "",
      title: res.main?.title || "",
      content: res.main?.content || "",
    };

    // ‚úÖ Ïö∞ÏÑ†ÏàúÏúÑ ÏΩîÎìú(BB1/BB2/BB3) Í∏∞Ï§ÄÏúºÎ°ú Î∞õÎäîÎã§Í≥† Í∞ÄÏ†ï
    priority.value = res.priority || "BB3";
    status.value = res.status || "";

    records.value =
      (res.details || []).map((d, idx) => ({
        id: d.detail_code || Date.now() + idx,
        counselDate: d.counsel_date || "",
        title: d.title || "",
        content: d.content || "",
      })) || [];

    attachments.value = res.attachments || [];

    await loadRejectionInfo();
  } catch (e) {
    console.error(e);
    error.value = e.message || "ÏÉÅÎã¥ Ï†ïÎ≥¥ Ï°∞Ìöå Ï§ë Ïò§Î•ò";
  } finally {
    loading.value = false;
  }
}

// üîπ Î∞òÎ†§ Ïù¥Î†• Ï°∞Ìöå
async function loadRejectionInfo() {
  try {
    const code = submitCode.value;

    if (!Number.isInteger(code) || code <= 0) {
      console.warn("[loadRejectionInfo] invalid submitCode:", {
        params: route.params.submitCode,
        query: route.query.submitCode,
      });
      rejectionInfo.value = { reason: "", date: "" };
      return;
    }

    const { data } = await axios.get(`/api/counsel/${code}/rejection-reason`);

    if (data?.success && data.result) {
      const r = data.result;
      rejectionInfo.value = {
        reason: r.rejection_reason || "",
        // SQLÏóêÏÑú approval_date AS rejection_date Î°ú ÎÇ¥Î†§Ï§ÄÎã§Í≥† Í∞ÄÏ†ï
        date: r.rejection_date || r.approval_date || "",
      };
    } else {
      rejectionInfo.value = { reason: "", date: "" };
    }
  } catch (e) {
    console.error("[loadRejectionInfo]", e);
    rejectionInfo.value = { reason: "", date: "" };
  }
}

/* ---------- ÏÉÅÌÉú ÎùºÎ≤® / Ïä§ÌÉÄÏùº ---------- */
function statusLabel(code) {
  const c = (code || "").toString().toUpperCase();
  switch (c) {
    case "CB1":
      return "ÏÉÅÎã¥Ï†Ñ"; // ÏûÑÏãúÏ†ÄÏû•
    case "CB2":
      return "ÏÉÅÎã¥Ï†Ñ";
    case "CB3":
      return "Í≤ÄÌÜ†Ï†Ñ";
    case "CB4":
      return "Î∞òÎ†§";
    case "CB5":
      return "Í≤ÄÌÜ†ÏôÑÎ£å";
    case "CB6":
      return "Ïû¨ÏäπÏù∏ÏöîÏ≤≠";
    default:
      return code || "-";
  }
}

// Î¶¨Ïä§Ìä∏ ÌôîÎ©¥Í≥º ÎèôÏùºÌïú ÌÅ¥ÎûòÏä§Î™Ö
function statusClass(code) {
  const c = (code || "").toString().toUpperCase();

  switch (c) {
    case "CB1": // ÏÉÅÎã¥Ï†Ñ (ÏûÑÏãú)
    case "CB2": // ÏÉÅÎã¥Ï†Ñ
      return "p-gray"; // ÌöåÏÉâ

    case "CB3": // Í≤ÄÌÜ†Ï†Ñ
      return "p-yellow"; // ÎÖ∏Îûë

    case "CB4": // Î∞òÎ†§
      return "p-red"; // Îπ®Í∞ï

    case "CB5": // Í≤ÄÌÜ†ÏôÑÎ£å
      return "p-green"; // Ï¥àÎ°ù

    case "CB6": // Ïû¨ÏäπÏù∏ÏöîÏ≤≠
      return "p-orange"; // Ï£ºÌô©

    default:
      return "p-gray"; // ÌòπÏãú Î™®Î•¥Îäî ÏÉÅÌÉúÎäî ÌöåÏÉâ
  }
}

function openSubmissionDetail() {
  const code = submitCode.value;
  if (!code) return;
  window.open(`/survey/submission/${code}`, "_blank");
}
function goBack() {
  router.push({ name: "counselList" });
}
function goEdit() {
  router.push({
    name: "counsel-edit",
    params: { submitCode: submitCode.value },
  });
}
function goWrite() {
  router.push({
    name: "counsel-new",
    params: { submitCode: submitCode.value },
  });
}

const rejectModalOpen = ref(false);
const rejectReason = ref("");

// ÏäπÏù∏
async function handleApprove() {
  try {
    const code = submitCode.value;
    const processorCode = auth.userCode; // üîπ Î°úÍ∑∏Ïù∏Ìïú Í¥ÄÎ¶¨Ïûê userCode

    if (!processorCode) {
      alert(
        "Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥Í∞Ä ÏóÜÏñ¥ ÏäπÏù∏ÏûêÎ•º Í∏∞Î°ùÌï† Ïàò ÏóÜÏäµÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥Ï£ºÏÑ∏Ïöî."
      );
      return;
    }

    const { data } = await axios.post(`/api/counsel/${code}/approve`, {
      processorCode, // üîπ Î∞±ÏóîÎìúÎ°ú Ï†ÑÏÜ°
    });

    if (data?.success) {
      alert("ÏäπÏù∏ÎêòÏóàÏäµÎãàÎã§.");
      await loadData(); // Îã§Ïãú Ï°∞Ìöå
    } else {
      alert(data.message || "ÏäπÏù∏ Ï≤òÎ¶¨ Ïã§Ìå®");
    }
  } catch (e) {
    console.error(e);
    alert("ÏÑúÎ≤Ñ Ïò§Î•ò: " + (e.response?.data?.message || e.message));
  }
}

// Î∞òÎ†§ Î≤ÑÌäº ÎàåÎ†ÄÏùÑ Îïå ‚Üí Î™®Îã¨ Ïó¥Í∏∞
function handleReject() {
  rejectReason.value = "";
  rejectModalOpen.value = true;
}

// Î™®Îã¨ ÏïàÏóêÏÑú 'Î∞òÎ†§' ÌôïÏ†ï
async function confirmReject() {
  if (!rejectReason.value.trim()) {
    alert("Î∞òÎ†§ ÏÇ¨Ïú†Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
    return;
  }

  try {
    const code = submitCode.value;
    const { data } = await axios.post(`/api/counsel/${code}/reject`, {
      reason: rejectReason.value,
    });
    if (data?.success) {
      alert("Î∞òÎ†§ÎêòÏóàÏäµÎãàÎã§.");
      rejectModalOpen.value = false;
      await loadData();
    } else {
      alert(data.message || "Î∞òÎ†§ Ï≤òÎ¶¨ Ïã§Ìå®");
    }
  } catch (e) {
    console.error(e);
    alert("ÏÑúÎ≤Ñ Ïò§Î•ò: " + (e.response?.data?.message || e.message));
  }
}

function closeRejectModal() {
  rejectModalOpen.value = false;
}

loadData();
</script>

<style scoped>
section {
  color: #111827;
}

/* ÏÉÅÎã® Ïï°ÏÖò ÎùºÏù∏ */
.form-action {
  margin-bottom: 0.75rem;
  padding-bottom: 0.5rem;
  display: flex;
  justify-content: space-between;
  gap: 0.75rem;
}

/* Î∞îÍπ• Ïπ¥Îìú */
.detail-card {
  background: #ffffff;
  border-radius: 0.9rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 10px 25px rgba(15, 23, 42, 0.05);
}

/* Ìó§Îçî */
.detail-header {
  padding-bottom: 0.75rem;
  margin-bottom: 1.25rem;
  border-bottom: 1px solid #e5e7eb;
}

/* ÏÉÅÌÉú pill Í≥µÌÜµ */
.status-pill {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.32rem 0.75rem;
  border-radius: 999px;
  font-size: 13px; /* ÏÇ¥Ïßù ÏûëÍ≤å */
  font-weight: 500;
  border: 1px solid transparent;
  min-width: auto; /* Í∏∏Ïù¥ Í∞ïÏ†úÌïòÏßÄ ÏïäÏùå */
  line-height: 1.1; /* Îçî ÏΩ§Ìå©Ìä∏ÌïòÍ≤å */
  height: auto;
}

/* Î©îÌÉÄ Ï†ïÎ≥¥ Ïπ¥Îìú */
.meta-card {
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  background-color: #f9fafb;
  padding: 0.9rem 1rem;
  margin-bottom: 1.25rem;
  font-size: 0.85rem;
}

.meta-row + .meta-row {
  margin-top: 0.35rem;
}
.meta-label {
  display: inline-block;
  width: 4.5rem;
  color: #6b7280;
}
.meta-value {
  color: #111827;
}

/* Ï°∞ÏÇ¨ÏßÄ Î≤ÑÌäºÏù¥ Îì§Ïñ¥Í∞ÄÎäî Ìñâ Ï†ïÎ†¨ */
.meta-row-inline {
  display: flex;
  align-items: center;
}
.meta-value-inline {
  display: inline-flex;
  align-items: center;
}
.meta-card,
.info-card,
.block-card {
  width: 100%;
}

/* ÏïàÎÇ¥ Ïπ¥Îìú */
.info-card {
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 0.9rem 1rem;
  font-size: 0.85rem;
  box-sizing: border-box;
}
